.chart-bar {
  height: 100%;
  width: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}
  
.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
  
.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.6rem;
    color: rgb(0, 0, 0);
  }
  